import React,{useEffect} from 'react'

import List1 from './List1'
import List2 from './List2'
import Cell from '../components/Cell/Cell'
import * as types from '../store/types'
import asyncAction from '../store/asyncAction'
import {connect} from 'react-redux'

const Liebiao = (props)=>{
    useEffect(()=>{
        props.getFollow()
    },[])

    let {follow}=props
        return (
            <div className='Liebiao'>
                <List1/>
                {
                    follow.map(item=>(
                        <Cell key={item._id} linkApi history={props.history} item={item} dataName='follow'/>
                    ))
                }
                <List2/>
            </div>
        )
}

const mapStateToProps = state =>({
    follow:state.follow
})

const mapDispatchToProps = dispatch =>({
    getFollow:()=>dispatch(asyncAction({
        apiname:'follow',
        params:{_page:1,_limit:7},
        typeName:types.UPDATE_FOLLOW
    })),

})

export default connect(mapStateToProps,mapDispatchToProps)(Liebiao)